@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/*product card*/
$p-card-space: 100px;
$p-card-space--lg: 50px;
$p-card-ratio: 60% 40%;
$p-card-ratio--lg: 50% 50%;

$p-card-elem-bottom-space: 40px;

.p-card {
	@include clear;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; } }

// title
.p-card__title {
	margin-bottom: $p-card-elem-bottom-space;

	h1, h2, h3 {
		@include font-alt-bold;
		@include font(#{$font-size-h2}, #{$line-height-h2});
		letter-spacing: 0.05em;
		margin-bottom: $p-card-elem-bottom-space;

		@include media('<mob-lg') {
			@include font(#{$font-size-h1--sm}, #{$line-height-h1--sm});
			margin-bottom: $bottom-space; }

		&:last-child {
			margin-bottom: 0; } }

	.to-catalog {
		text-align: right;
		margin-bottom: $bottom-space; } }

.p-card__state {
	@include font-d();

	.p-card__model, .p-card__store {
		@include display-inline-block-top; } }

.p-card__model {
	color: $text-color-light; }

.p-card__store {
	&::before {
		content: "/";
		display: none;
		color: $text-color-light; }

	.p-card__model + &::before {
		display: inline-block; }

	svg {
		@include display-inline-block-top;
		@include size(14px);
		margin-top: 3px; }

	path, polygon {
		fill: inherit; }

	&.on {
		$color: #32b39b;
		// ================
		color: $color;
		fill: $color; }

	&.off {
		$color: #ff203b;
		// ================
		color: $color;
		fill: $color;

		svg {
			@include size(10px);
			margin-top: 5px; } } }

// product photos
.p-card__visual, .p-card__content__holder {
	padding: 0 $p-card-space; }

.p-card__visual {
	float: left;
	width: nth($p-card-ratio, 1);

	@include media('<desk-lg') {
		width: nth($p-card-ratio--lg, 1); } }

.p-card__gallery {
	margin-bottom: $bottom-space--lg;

	&:last-child {
		margin-bottom: 0; } }

.p-card__gallery__item {
	height: calc(100vh - 264px); // костыль
	padding-top: $p-card-space;

	&:first-child {
		padding-top: 0; }

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(none);
		@include object-fit(scale-down); } }

// widget
.p-card__widget {
	margin-bottom: $p-card-elem-bottom-space;
	@include font(#{$font-size}, #{$line-height}); }

.p-card__widget__title {
	display: block;
	@include font-d();
	margin-bottom: $bottom-space--sm; }

// info
.p-card__content {
	width: nth($p-card-ratio, 2);
	float: right;
	clear: right;

	@include media('<desk-lg') {
		width: nth($p-card-ratio--lg, 2); } }

.p-card__content__holder {
	padding-left: 0; }

// price
.p-card__price {
	@include font-normal;
	line-height: 24px;
	color: $base-color;

	.val {
		display: inline-block;
		vertical-align: baseline;
		@include font-bold;
		font-size: 18px; }

	small {
		display: inline-block;
		vertical-align: baseline;
		font-size: 14px; }

	.old, .cur {
		position: relative;
		display: inline-block;
		vertical-align: baseline;
		margin-right: $grid-space; }

	.old {
		color: $text-color-light;

		&::after {
			@include pseudo();
			@include size(calc(100% + 4px), 0);
			left: -2px;
			bottom: 50%;
			margin-bottom: -1px;
			border-top: 1px solid $text-color-light; }

		.val {
			font-size: 14px; }

		small {
			font-size: 10px; } } }

// list of characteristics
.p-card__options__dl {
	margin-bottom: $p-card-elem-bottom-space;
	$dt-width: 184px;

	&:last-child {
		margin-bottom: 0; }

	.p-card__options__di {
		position: relative;
		padding: 10px 0;
		border-bottom: 1px dashed $text-color-light;
		@include clear; }

	.p-card__options__dt {
		float: left;
		width: $dt-width;
		color: $text-color-light;
		padding-right: 10px; }

	.p-card__options__dd {
		margin-left: $dt-width; } }

// list of grid view parameters
.p-card__options__grid {
	@include clear-spacing;

	&, .#{$user-content} & {
		padding: 0 0 1px 1px;
		margin: 0 0 $p-card-elem-bottom-space;

		&:last-child {
			margin-bottom: 0; }

		li {
			padding: 8px;
			margin: 0 0 -1px -1px;

			&::before {
				content: none; } } }

	li {
		@include display-inline-block-top;
		min-width: 61px;
		border: 1px solid $border-color;
		@include font(16px, 20px);
		letter-spacing: normal;
		text-align: center; } }

// list of colors
.p-card__color {
	@include clear-spacing;

	&, .#{$user-content} & {
		padding: 0 0 1px 1px;
		margin: 0 0 $p-card-elem-bottom-space;

		&:last-child {
			margin-bottom: 0; }

		li {
			padding: 8px 8px 8px 42px;
			margin: 0 0 -1px -1px;

			&::before {
				content: none; } } }

	li {
		position: relative;
		@include display-inline-block-top;
		width: 33.28%;
		border: 1px solid $border-color;
		@include font($font-size, $line-height);
		letter-spacing: normal;
		@include truncate(none);
		overflow: hidden;
		cursor: default; }

	.color-pic {
		position: absolute;
		left: 19px;
		top: 14px;
		margin: 0;
		z-index: 5;
		will-change: transform;
		transition: {
			property: transform;
			duration: 0.6s; } }

	.no-touchevents & li:hover {
		.color-pic {
			$scale: 30;
			transform: scale($scale);
			transform: scale3d($scale, $scale, $scale); } } }

/*product card end*/

/*card bar*/
.p-card-overview {
	position: relative;
	margin-bottom: $p-card-elem-bottom-space;

	&:last-child {
		margin-bottom: 0; } }

/*card bar end*/
