.shutter {
	overflow: hidden; // необходим для скрытия скролла
	position: fixed;
	left: #{$sidebar-width};
	bottom: 0;
	@include size(100%, calc(100vh - #{$header-height}));
	min-width: 320px;
	max-width: $shutter-width;
	//padding: 0 0 120px
	background: $bg-color;
	opacity: 0;
	visibility: hidden;
	z-index: 1000;

	@include media('<tab-lg') {
		left: 0;
		//top: #{$top-panel-height--md}
		//height: calc(100vh - #{$top-panel-height--md})
		max-width: none;
		padding-top: 0; } }

.shutter__align {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%;

	@include media('<tab-lg') {
		width: 100%; } }

.shutter__holder {
	width: #{$shutter-width};
	//padding: nth($shutter-space, 1) nth($shutter-space, 2)
	@include clear;

	@include media('<tab-lg') {
		width: 100%; } }

.shutter__footer {
	border-top: 1px solid $border-color; }

/* shutter content */
.shutter-content {
	float: left;
	width: calc(100% - #{$shutter-aside-width}); }

/* shutter content end */

/* shutter aside */
.shutter-aside {
	float: right;
	width: $shutter-aside-width; }

/* shutter aside end */

/* button shutter close */
.shutter {
	.btn-popup-close {
		display: none;
		position: absolute;
		right: 0;
		top: 0;

		@include media('<tab-lg') {
			display: block; } } }

/* button shutter close end */

/* shutter wide */
.shutter--wide {
	max-width: calc(100% - #{$sidebar-width});

	@include media('<tab-lg') {
		max-width: 100%; }

	.shutter__align {
		width: 100%; }

	.shutter__holder {
		width: 100%; } }

/* shutter wide end */

/* bag shutter */
.bag-shutter {
	$bag-shutter-width: 470px;
	// ======================
	width: $bag-shutter-width;
	max-width: 100%;
	left: auto;
	right: 0;

	.shutter__align {
		overflow-y: hidden; }

	.shutter__holder {
		width: #{$bag-shutter-width};
		height: 100%;
		padding: 0;

		&::after {
			content: none; }

		@include media('<tab-lg') {
			width: 100%; } }

	.shutter__row {
		overflow-x: hidden;
		overflow-y: auto;

		&.shutter__footer {
			overflow-y: hidden; } }

	.shutter__cell {
		padding: $grid-space; } }

/* bag shutter end */
/* shutter end */
